@import '~styles/variables.scss';
@import '~styles/mixins.scss';

$max-portrait-width: 600px;

@mixin max-portrait {
  @media (max-width: $max-portrait-width) {
    @content;
  }
}

.root {
  display: flex;
  text-align: center;
  justify-content: center;

  .wrap {
    width: 78%;
    margin: auto;

    @include media-portrait {
      width: 100%;
    }
  }

  .description {
    font-size: 16px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 2.19;
    letter-spacing: 0.4px;
    text-align: center;
    color: $color-black;
    margin-top: 30px;

    @include media-mobile-xl {
      // padding: 0 10px;
    }
  }

  .input {
    width: 100%;
    max-width: 795px;
    margin-top: 60px;
    margin-bottom: 40px;
    padding: 34px 45px 34px 40px;

    &.errored {
      border-color: $color-electric-violet;
      color: $color-electric-violet;
    }

    @include max-portrait {
      padding: 20px;
    }
  }

  .button {
    width: 300px;
    position: relative;
    z-index: 1;
  }

  @include max-portrait {
    .button {
      width: 100%;
    }
  }
}
