@import '~styles/variables.scss';
@import '~styles/mixins.scss';

:global .headingH2 {
  font-size: 40px;
  line-height: normal;
  font-weight: 900;
  text-align: center;
  letter-spacing: 1.5px;
  color: $color-text;

  @include before-media-mobile {
    font-size: 30px;
    line-height: 1.33;
    letter-spacing: 0.6px;
  }
}

:global .headingH3 {
  font-size: 30px;
  line-height: normal;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.4px;
  color: $color-text;
}

:global .simpleText {
  font-size: 16px;
  font-weight: 300;
  line-height: 2.19;
  letter-spacing: 0.4px;
  text-align: center;
  color: $color-text;

  @include before-media-mobile {
    font-size: 12px;
    line-height: 2.08;
    letter-spacing: 0.3px;
  }
}
