@font-face {
  font-family: 'Montserrat';
  src: url('./fonts/Montserrat-Black.woff2') format('woff2'), /* Modern Browsers */
       url('./fonts/Montserrat-Black.woff') format('woff'), /* Modern Browsers */
       url('./fonts/Montserrat-Black.ttf') format('truetype');
  font-style: normal;
  font-weight: 900;
}

