:global .error-enter {
  opacity: 0.01;
}

:global .error-enter.error-enter-active {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

:global .error-leave {
  opacity: 1;
}

:global .error-leave.error-leave-active {
  opacity: 0.01;
  transition: opacity 0.3s ease-in;
}

:global {
  .fade-enter {
    opacity: 0.01;
  }

  .fade-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }

  .fade-exit {
    opacity: 1;
  }

  .fade-exit-active {
    opacity: 0.01;
    transition: opacity 500ms ease-in;
  }
}
