@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  height: $height-footer;
  background-color: $color-white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  & > div {
    padding: 15px 0;
  }

  .links {
    display: flex;
    justify-content: center;

    & > a {
      font-size: 13px;
      font-weight: 900;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      text-align: center;
      color: $color-black;
      text-transform: uppercase;
      padding: 0 15px;
      letter-spacing: 0.9px;
      transition: color 0.3s ease-out;

      &.email {
        font-weight: bold;
        letter-spacing: 0.3px;
        color: $color-hover;
      }

      @include hover {
        color: $color-hover;
      }
    }
  }

  @include media-portrait {
    .links {
      flex-wrap: wrap;

      & > a {
        width: 50%;
        padding: 10px 0;

        &.email {
          width: 100%;
        }
      }
    }
  }

  @include media-mobile {
    .links {
      & > a {
        font-size: 11px;
        letter-spacing: 0.7px;

        &.email {
          letter-spacing: 0.3px;
          width: 100%;
        }
      }
    }
  }

  .copyright {
    font-size: 13px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $color-scorpion;
  }
}
