@import '~styles/variables.scss';
@import '~styles/mixins.scss';

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fade-in-down {
  0% {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes fade-out-down {
  0% {
    opacity: 1;
    transform: none;
  }

  100% {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
}

@keyframes increase-width {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

.root {
  background: $color-white;
  position: fixed;
  padding-top: 26vh;
  text-align: right;
  height: calc(100vh - (#{$height-header} - 2px));
  width: 100vw;
  top: calc((#{$height-header} - 2px) - 70px);
  left: 0;
  z-index: 1003;
  overflow: auto;
  padding-right: 30px;
  box-sizing: border-box;
  animation-name: fade-in;
  animation-duration: 0.35s;
  animation-timing-function: cubic-bezier(0.7, 0.17, 0.18, 0.77);
  animation-fill-mode: forwards;
  transform: translateY(70px);

  @include before-media-mobile {
    padding-top: 25px;
  }

  @include media-mobile {
    top: calc(#{$height-header-mobile} - 70px);
    height: calc(100vh - #{$height-header-mobile});
  }

  &.fixed {
    top: calc(#{$fixed-height-header} - 70px);
    height: calc(100vh - #{$fixed-height-header});
  }
}

.closed {
  animation-name: fade-out;
  animation-delay: 0.3s;
  animation-duration: 0.3s;
  animation-timing-function: cubic-bezier(0.7, 0.17, 0.18, 0.77);
  animation-fill-mode: forwards;
}

:global .noscroll-menu {
  height: 100%;
  overflow: hidden !important;
  width: 100%;
}

.current {
  color: rgb(172, 172, 172);
  margin-left: 15px;
  cursor: pointer;
  text-decoration: none;
  pointer-events: none;
}

.disabled {
  color: $color-black !important;
  margin-left: 15px;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

.disabled:hover::after {
  display: block;
  content: '';
  height: 2px;
  width: 0;
  animation-name: increase-width;
  animation-duration: 0.5s;
  animation-timing-function: cubic-bezier(0, 0.16, 0.12 0, 1);
  animation-fill-mode: forwards;
  background-color: $color-black;
}

.list {
  text-align: right;
  margin-right: 10px;

  @include media-mobile {
    margin-right: 13px;
  }

  & > a {
    display: block;
  }
}

.links {
  text-align: right;
  padding-top: 15px;
  margin-right: 10px;

  @include media-mobile {
    margin-right: 13px;
  }
}

.email {
  animation-name: fade-in-down;
  animation-duration: 0.23s;
  animation-delay: 0.58s;
  animation-timing-function: cubic-bezier(0.12, 0.51, 0.49, 0.88);
  animation-fill-mode: both;
  height: 35px;
  margin-bottom: 2vh;
}

.email-closed {
  animation-name: fade-out-down;
  animation-duration: 0.115s;
  animation-delay: 0.29s;
  animation-timing-function: cubic-bezier(0.12, 0.51, 0.49, 0.88);
  animation-fill-mode: both;
}

.email > a {
  text-decoration: none;
  display: inline-block;
  color: $color-hover;
  font-size: 11px;
  letter-spacing: 0.3px;
  font-weight: 600;
  text-transform: uppercase;

  @include hover {
    &::after {
      position: relative;
      display: block;
      content: '';
      height: 2px;
      width: 0;
      animation-name: increase-width;
      animation-duration: 0.5s;
      animation-timing-function: cubic-bezier(0, 0.16, 0.12 0, 1);
      animation-fill-mode: forwards;
      background-color: black;
    }
  }
}

.contact {
  height: 29px;
  animation-name: fade-in-down;
  animation-duration: 0.23s;
  animation-delay: 0.58s;
  animation-timing-function: cubic-bezier(0.12, 0.51, 0.49, 0.88);
  animation-fill-mode: both;

  a {
    font-size: 11px;
    font-weight: 900;
    letter-spacing: 0.7px;
    text-transform: uppercase;

    &:hover {
      color: $color-hover;
    }
  }
}

.logo {
  align-items: center;
  display: flex;
  opacity: 0.2;
  width: 35px;
  margin-left: 10px;

  @include hover {
    opacity: 1;
    cursor: pointer;
  }
}
