@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  &.defaultStyles {
    padding: 21px 19px 19px 20px;
    color: $color-text;
    border: 1px solid $color-not-active-bg1;
    border-radius: 4px;
    font-size: 14px;
    line-height: normal;
    letter-spacing: 0;

    &::placeholder {
      color: $color-gray;
    }

    &:focus {
      border-color: $color-alto;
    }

    @include before-media-mobile {
      padding: 15px;
    }
  }
}
