@import '~styles/variables.scss';
@import '~styles/mixins.scss';

$max-portrait-width: 600px;

@mixin max-portrait {
  @media (max-width: $max-portrait-width) {
    @content;
  }
}

.root {
  display: inline-block;
  background-color: $color-white;
  padding: 30px 40px 34px 40px;
  box-shadow: 10px 20px 40px 0 rgba(0, 0, 0, 0.07);
  border-radius: 4px;

  .title {
    text-align: left;
    margin-bottom: 25px;

    @include media-desktop {
      font-size: 24px;
      letter-spacing: 0.3px;
      margin-bottom: 20px;
    }
  }

  @include max-portrait {
    width: 100%;
  }

  @include before-media-mobile {
    padding: 20px;

    .title {
      font-size: 18px;
      letter-spacing: 0.2px;
      margin-bottom: 12px;
    }

    .input {
      display: none;
    }
  }
}

.upperTitle {
  font-size: 16px;
  line-height: normal;
  font-weight: 900;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: $color-light-alto;
  margin-bottom: 10px;

  @include media-desktop {
    margin-bottom: 7px;
  }

  @include media-portrait {
    font-size: 14px;
    letter-spacing: 0.4px;
  }

  @include before-media-mobile {
    font-size: 12px;
    letter-spacing: 0.4px;
    margin-bottom: 6px;
  }
}

.wrapper {
  display: flex;
  align-items: center;

  @include max-portrait {
    flex-wrap: wrap;
  }
}

.input {
  width: 300px;
  flex-shrink: 0;
  margin-right: 13px;

  &.errored {
    border-color: $color-electric-violet;
    color: $color-electric-violet;
  }

  @include media-desktop {
    width: 240px;
  }

  @include max-portrait {
    width: 100%;
    margin-bottom: 12px;
  }
}

.button {
  height: 60px;
  min-height: 60px;
  width: 158px;
  padding-left: 25px;
  padding-right: 25px;

  @include max-portrait {
    width: 100%;
  }

  @include before-media-mobile {
    min-height: 50px;
    height: 50px;
  }
}
