@import '~styles/variables.scss';
@import '~styles/mixins.scss';

@keyframes top-open {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }

  50% {
    transform: translate3d(0, 10px, 0) rotate(0deg);
  }

  100% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
}

@keyframes bottom-open {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }

  50% {
    transform: translate3d(0, -7px, 0) rotate(0deg);
  }

  100% {
    transform: translate3d(0, -7px, 0) rotate(-45deg);
  }
}

@keyframes middle-out {
  60% {
    opacity: 1;
  }

  70% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes top-close {
  0% {
    transform: translate3d(0, 12px, 0) rotate(45deg);
    bottom: 10px;
  }

  50% {
    transform: translate3d(0, 12px, 0) rotate(0deg);
    bottom: 10px;
  }

  100% {
    transform: translateY(0) rotate(0deg);
    bottom: 5px;
  }
}

@keyframes bottom-close {
  0% {
    transform: translate(0, -7px) rotate(-45deg);
  }

  50% {
    transform: translate(0, -7px) rotate(0deg);
  }

  100% {
    transform: translateY(0) rotate(0deg);
  }
}

@keyframes middle-in {
  0% {
    opacity: 0;
  }

  30% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.root {
  cursor: pointer;
  z-index: 10;
  margin-right: 10px;
  margin-top: 2px;
  display: block;

  & > div {
    display: block;
    position: relative;
    width: 38px;
    height: 28px;

    & > .top {
      top: 0;
    }

    & > .bottom {
      bottom: 0;
    }

    & > .middle {
      margin: auto;
      top: 13px;
      transition: width 0.4s ease-out;
    }

    & > .icon {
      position: absolute;
      width: 38px;
      height: 30px;
      height: 2px;
      background-color: $color-black;
    }
  }

  .closed {
    & > .top {
      transform-origin: center;
      animation-name: top-close;
      animation-duration: 0.4s;
    }

    & > .bottom {
      animation-name: bottom-close;
      animation-duration: 0.4s;
    }

    & > .middle {
      animation-name: middle-in;
      animation-duration: 0.4s;
    }
  }

  .open {
    & > .icon {
      position: absolute;
      height: 2px;
      width: 100%;
      background-color: $color-black;
    }

    & > .top {
      transform-origin: center;
      animation-name: top-open;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
      top: 3px;
    }

    & > .middle {
      animation-name: middle-out;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
      top: 48%;
    }

    & > .bottom {
      transform-origin: center;
      animation-name: bottom-open;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
      bottom: 6px;
    }
  }

  @include hover {
    .closed div {
      background-color: $color-hover;
    }

    .closed .middle {
      width: 31px;
    }

    .open div {
      background-color: $color-hover;
    }
  }

  &:active .closed {
    & > .middle {
      width: 0;
    }
  }

  @include media-mobile {
    width: 29px;
    margin-right: 13px;

    & > div {
      width: 29px;
      height: 26px;

      & > .top {
        top: 1px;
      }

      & > .bottom {
        bottom: 3px;
      }

      & > .middle {
        top: 11px;
      }
    }

    .closed {
      & > .icon {
        width: 29px;
      }
    }

    .open {
      & > .top {
        top: 0;
      }

      & > .bottom {
        bottom: 7px;
      }
    }
  }
}
