@import '~styles/variables.scss';

.root {
  width: 100%;
  min-height: 65px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 19px 0 23px;
  border-radius: 4px;
  background-color: $color-not-active-bg1;
}

.title {
  font-size: 14px;
  line-height: normal;
  color: $color-scorpion;
  margin-right: 10px;
}

.cross {
  margin-left: auto;

  svg {
    fill: $color-gray;
  }
}
