@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  .wrap {
    width: 78%;
    margin: auto;

    @include media-portrait {
      width: 100%;
    }
  }

  .btn {
    width: 300px;
    height: 65px;

    @include media-portrait {
      width: 100%;
      position: relative;
      z-index: 1;
    }
  }

  .mail-logo {
    text-align: center;
    margin-bottom: 60px;

    @include media-portrait {
      margin-bottom: 26px;
    }

    svg {
      width: 220px;
      height: 190px;

      @include media-portrait {
        height: 132px;
      }
    }
  }

  .description {
    font-size: 16px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 2.19;
    letter-spacing: 0.4px;
    text-align: center;
    color: $color-black;
    margin-top: 30px;
    margin-bottom: 60px;
  }
}
