@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  width: 100%;
  min-height: 65px;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  padding: 0 26px;
  border-radius: 4px;
  background-color: $color-not-active-bg1;
  cursor: pointer;
  user-select: none;
  transition: background-color $default-duration-animation ease-out;

  @include hover {
    background-color: $color-alto;
  }

  &:active {
    background-color: $color-wild-sand;
  }

  @include before-media-mobile {
    padding: 0 25px;
  }
}

.icon {
  width: 19px;
  height: 21px;
  margin-right: 8px;
}

.title {
  font-size: 14px;
  line-height: normal;
  color: $color-scorpion;
}
