@import '~styles/variables.scss';
@import '~styles/mixins.scss';

$animation-duration: 0.4s;

@keyframes login_btn {
  0% {
    transform: translate3d(0, 0, 0);
  }

  40% {
    transform: translate3d(-3px, -3px, 0);
  }

  60% {
    transform: translate3d(-2px, -2px, 0);
  }

  100% {
    transform: translate3d(-3px, -3px, 0);
  }
}

.root {
  min-height: 65px;
  background-color: $color-black;
  color: $color-white;
  text-transform: uppercase;
  font-size: 15px;
  line-height: normal;
  font-weight: 900;
  padding: 0 30px;
  text-align: center;
  border: none;
  transition: background-color $middle-duration-animation $default-bezier, box-shadow $middle-duration-animation $default-bezier, color $middle-duration-animation $default-bezier;
  letter-spacing: 1px;
  cursor: pointer;
  backface-visibility: hidden;

  @include hover {
    background-color: $color-hover;
    box-shadow: 15px 15px 20px rgba(0, 239, 254, 0.4);
    animation: login_btn $animation-duration $default-bezier forwards;

    @include media-portrait {
      animation: none;
    }
  }

  &.login {
    transition: background-image $middle-duration-animation $default-bezier, box-shadow $middle-duration-animation $default-bezier, color $middle-duration-animation $default-bezier;
    background-image: linear-gradient(45deg, $color-not-active-bg1 0%, $color-not-active-bg2 100%);

    @include hover {
      background-image: linear-gradient(45deg, $color-hover 0%, $color-hover 100%);
      box-shadow: 15px 15px 20px rgba(0, 239, 254, 0.4);
      animation: login_btn $animation-duration $default-bezier forwards;

      @include media-portrait {
        animation: none;
      }
    }

    :global(.noDevice) &:active {
      box-shadow: none;
    }
  }

  :global(.noDevice) &:active {
    box-shadow: none;
  }

  &[disabled] {
    background: linear-gradient(45deg, $color-not-active-bg1 0%, $color-not-active-bg2 100%);
    box-shadow: none;
    cursor: not-allowed;
  }

  i {
    margin-right: 10px;
    padding-top: 4px;

    svg {
      width: 25px;
      height: 25px;
    }
  }

  @include media-portrait {
    letter-spacing: 1px;
  }
}
