@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.subheader {
  font-size: 16px;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #d9d9d9;
  text-transform: uppercase;
  margin-top: 28px;
  margin-bottom: 60px;
  display: block;
}

.root {
  text-align: left;

  p {
    margin-bottom: 20px;
    text-align: left;
  }

  ul {
    list-style-type: disc;
    list-style-position: inside;
    padding-bottom: 20px;
    text-align: left;
  }

  b {
    font-weight: 700;
  }

  a {
    font-size: 13px;
    font-weight: 900;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    text-align: center;
    color: $color-black;
    text-transform: uppercase;
    letter-spacing: 0.9px;
    transition: color 0.3s ease-out;

    &.email {
      font-weight: bold;
      letter-spacing: 0.3px;
      color: $color-hover;
    }

    @include hover {
      color: $color-hover;
    }
  }
}
