@import '~styles/variables.scss';
@import '~styles/mixins.scss';

$min-height-section: 600px;
$max-height-section: 1440px;

.root {
  font-style: normal;
}

.main {
  min-height: $min-height-section;
  max-height: $max-height-section;
  height: calc(100vh - #{$height-header});
  position: relative;
  z-index: 100;
  overflow: visible;
  background-color: $color-white;

  @include before-media-mobile {
    min-height: $min-height-section-before-mobile;
  }

  @include media-mobile {
    min-height: $min-height-section-mobile;
    height: calc(100vh - #{$height-header-mobile});
  }
}

.about {
  background-color: transparent;
  position: relative;
  z-index: 2;
  overflow: visible;
  min-height: calc((100vh - #{$height-header}) * 2);
  height: auto;
  margin-top: calc(-100vh + #{$height-header});
  padding-bottom: 10px;

  @include before-media-mobile {
    min-height: calc((100vh - #{$height-header}) * 2);
    height: auto;
  }

  @include media-mobile {
    min-height: calc((100vh - #{$height-header-mobile}) * 2);
    height: auto;
    margin-top: calc(-100vh + #{$height-header-mobile});
  }
}

.bublic {
  width: 52vh;
  height: 52vh;
  margin: 0 auto;
  position: sticky;
  bottom: 34.5vh;
  transform-origin: center top;
  z-index: 1;
  backface-visibility: hidden;

  @include media-hd {
    bottom: 33.5vh;
  }

  @include media-portrait {
    width: 42vh;
    height: 42vh;
    bottom: 37.5vh;
  }

  @include before-media-mobile {
    width: 45vh;
    height: 45vh;
    bottom: 36vh;
  }

  @include edge_check {
    position: absolute;
    left: 0;
    right: 0;
  }
}
