@import '~styles/mixins.scss';
@import '~styles/variables.scss';

.root {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
  color: $color-gray;
  user-select: none;
  line-height: 0;

  svg {
    fill: $color-black;
    transition: fill 0.3s ease-out, transform 0.3s ease-out;
    backface-visibility: hidden;

    circle {
      r: 0;
      transition: r 0.3s ease-out;
    }
  }

  @include hover {
    svg {
      fill: $color-aqua;
      transform: translateZ(0) scale(1.133, 1.133);

      circle {
        r: 3;
      }
    }
  }

  &:active {
    svg {
      color: $color-bright-turquoise;
      transform: scale(1.4);

      circle {
        r: 6;
      }
    }
  }
}
