@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  font-style: normal;
  text-align: center;
}

.field {
  width: 100%;
  max-width: 980px;
  margin: 0 auto;
  margin-bottom: 40px;
  display: flex;
  align-items: flex-start;
  background-color: $color-white;
  color: $color-text;
  border: 1px solid $color-not-active-bg1;
  border-radius: 4px;
  padding: 13px 11px 12px 40px;

  &::placeholder {
    color: $color-gray;
  }

  &.errored {
    border-color: $color-electric-violet;
    color: $color-electric-violet;
  }

  &.isFocused {
    border-color: $color-alto;
  }

  @include media-hd {
    max-width: 795px;
  }

  @include media-desktop {
    max-width: 884px;
  }

  @include media-landscape {
    max-width: 670px;
  }

  @include media-portrait {
    max-width: 700px;
    padding-left: 20px;
  }

  @include before-media-mobile {
    flex-direction: column;
    padding: 10px;
    padding-top: 22px;
    margin-bottom: 30px;
  }
}

.input {
  width: 100%;
  min-height: 65px;
  padding: 5px 10px 5px 0;
  flex-shrink: 1;
  font-size: 18px;
  line-height: normal;
  color: $color-text;

  &::placeholder {
    color: $color-gray;
  }

  @include media-portrait {
    padding-left: 20px;
  }

  @include before-media-mobile {
    min-height: 18px;
    padding: 1px 0 0 8px;
    font-size: 14px;
    margin-bottom: 23px;
  }
}

.uploader {
  max-width: 190px;
  width: 100%;
  flex-shrink: 0;

  @include before-media-mobile {
    max-width: 500px;
  }
}

.button {
  width: 100%;
  max-width: 300px;
}
