@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.bublikWrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: visible;
  z-index: 3;
  clip: rect(0, auto, auto, 0);
}

.likeText {
  height: 100%;
  width: 100%;
}

.bublik {
  z-index: 111;
  bottom: calc((100vh + 34.5vh - #{$height-header}));

  @include edge_check {
    transform: translate3d(0, 18%, 0) scale(1.5);
  }

  @include media-hd {
    bottom: calc((100vh + 33.5vh - #{$height-header}));
  }

  @include media-portrait {
    bottom: calc((100vh + 37.5vh - #{$height-header}));
  }
}

.paddingTop {
  position: relative;
  z-index: 4;
  padding-top: calc(100vh - #{$height-header});

  @include media-mobile {
    padding-top: calc(100vh - #{$height-header-mobile});
  }
}

.indexWrapper {
  z-index: 0;
  position: relative;
}

.footerGradient {
  height: 52vh;
  width: 75vw;
  background-image: linear-gradient(0deg, rgba(255, 255, 255, 1) 10%, rgba(255, 255, 255, 0.8) 20%, rgba(255, 255, 255, 0.7) 30%, rgba(255, 255, 255, 0.6) 40%, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0.3) 60%, rgba(255, 255, 255, 0.1) 80%, rgba(255, 255, 255, 0.01) 100%);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 4;

  @include media-portrait {
    height: 42vh;
    width: 100vw;
  }

  @include before-media-mobile {
    height: 45vh;
  }
}

.headerGradientWrapper {
  width: 100%;
  height: 33vh;
  margin-bottom: 30px;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 1;

  @include media-desktop {
    height: 30.5vh;
  }

  @include media-landscape {
    margin-bottom: 15px;
  }

  @include before-media-mobile {
    height: 10.5vh;
    background-image: none;
  }
}

.headerGradient {
  // stylelint-disable-next-line
  height: 10vh;
  background-image: linear-gradient(to top, rgba(5, 6, 8, 0) 0%, rgba(0, 5, 5, 0.04) 100%);
}

.content {
  margin: 0 17.4%;

  @include media-hd {
    margin: 0 9.7%;
  }

  @include media-desktop {
    margin: 0 11%;
  }

  @include media-landscape {
    margin: 0 14%;
  }

  @include media-portrait {
    margin: 0 10px;
  }
}

.title {
  text-align: center;
  margin-bottom: 30px;

  @include before-media-mobile {
    margin-bottom: 18px;
  }
}

.paragraf {
  margin-bottom: 25px;
  text-align: center;

  &:last-child {
    margin-bottom: 0;
  }
}
