.root {
  font-style: normal;
}

.gray,
.first {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.firstWrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 101%;
  height: 101%;
  overflow: hidden;
  z-index: 10;
}
