@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  height: $height-header;
  padding-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $color-white;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 100%;
  position: fixed;
  z-index: 1001;
  transition-delay: 0s;
  transition-duration: 0.2s;
  transition-property: padding, box-shadow, height;
  transition-timing-function: $default-bezier;

  &.fixed {
    padding-top: 0;
    height: $fixed-height-header;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.05);
  }

  .logotype {
    margin-left: -3px;
    margin-top: 3px;

    svg {
      width: 127px;
      height: 27px;
    }
  }

  .login {
    width: 140px;
    min-height: 50px;
    margin-left: 15px;
    font-size: 15px;
    font-weight: 900;
    letter-spacing: 1px;
    margin-top: -3px;
  }

  .nav {
    display: flex;
    align-items: center;
  }

  @include media-portrait {
    .login {
      display: none;
    }
  }

  @include media-mobile {
    height: $height-header-mobile;
    padding-top: 25px;

    .logotype {
      margin-left: 2px;
      margin-top: -1px;
    }
  }
}

:global {
  .header-resize {
    transition: none;
  }
}
