@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  font-style: normal;
  background-color: $color-wild-sand;
  padding-top: 80px;
  padding-bottom: 95px;
}

.title {
  margin-bottom: 10px;

  @include before-media-mobile {
    margin-bottom: 20px;
  }
}

.subTitle {
  font-size: 16px;
  line-height: 2.19;
  font-weight: 300;
  letter-spacing: 0.4px;
  text-align: center;
  color: $color-text;
  margin-bottom: 40px;

  @include before-media-mobile {
    padding: 0 16%;
    font-size: 12px;
    line-height: 2.08;
    letter-spacing: 0.3px;
  }
}
