@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  box-shadow: 0 -30px 41px -12px rgba(255, 255, 255, 0.89);
  position: relative;
  z-index: 5;

  .teamItem {
    visibility: hidden;

    &.teamOpen {
      visibility: visible;
    }
  }

  h3 {
    line-height: 139px;
  }

  .team {
    display: flex;
    flex-wrap: wrap;
    background-color: $color-racing-green;
    overflow: hidden;
    transition: height 1s ease-out;

    @include media-mobile {
      margin-top: -20px;
    }
  }

  .show-more {
    text-align: center;
    height: 175px;
    padding-top: 50px;
  }

  .btn {
    border-radius: 50px;
    font-size: 14px;
    text-transform: none;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: $color-scorpion;
    width: 160px;
    min-height: 45px;
    padding: 0 20px;
    background-color: $color-wild-sand;

    i {
      margin-right: 10px;
      display: inline-block;
      vertical-align: middle;
    }

    span {
      vertical-align: middle;
      display: inline-block;
    }

    i > svg {
      width: 19px;
      height: 19px;
      stroke: $color-gray;
      transition: stroke 0.3s ease-out;
    }

    @include hover {
      background-color: $color-hover;
      box-shadow: 10px 10px 15px rgba(0, 239, 254, 0.4);
      color: $color-white;

      i > svg {
        width: 19px;
        height: 19px;
        stroke: $color-white;
      }
    }

    &:active {
      background-color: $color-black;
      box-shadow: none;
    }
  }
}
