@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  padding: 2% 9.5% 0 9.5%;
  font-size: 16px;
  line-height: 35px;
  letter-spacing: 0.4px;
  font-weight: 300;
  text-align: center;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  height: 100vh;

  .cross {
    top: 10%;
    right: 8%;
    position: absolute;
    margin-right: 7px;
    transition: fill 0.3s ease-out;
    z-index: 10;

    circle {
      fill: transparent;
    }

    @include hover {
      circle {
        fill: $color-white;
      }
    }

    @include media-portrait {
      // padding: 10% 0 0 0;
      padding: 0;

      .cross {
        top: 26px;
        right: calc(4.4% + 6px);
        margin-right: 0;
        width: 28px;
        height: 28px;
        background-color: $color-white;
        z-index: 10;

        &.initial {
          top: 60px;
          right: calc(4.4% + 6px);
        }
      }

      & + button svg {
        background-color: $color-white;
      }
    }

    @include media-mobile {
      .cross {
        top: 20px;
        right: 29px;
        width: 22px;
        height: 22px;

        &.initial {
          top: 32px;
          right: 29px;
        }
      }

      & + button svg {
        width: 24px;
        height: 24px;
        margin-right: 7px;
      }
    }
  }

  &.isTouch {
    padding-left: 1.5%;
    padding-right: 1.5%;

    @include media-portrait {
      // padding: 10% 0 0 0;
      padding: 0;

      .cross {
        top: 26px;
        right: calc(4.4% + 6px + 2.2%);
        margin-right: 0;
        width: 28px;
        height: 28px;
        background-color: $color-white;
        z-index: 10;

        &.initial {
          top: 60px;
          right: calc(4.4% + 6px + 2.2%);
        }
      }

      & + button svg {
        background-color: $color-white;
      }
    }

    @include media-mobile {
      .cross {
        top: 24px;
        right: 36px;
        width: 22px;
        height: 22px;

        &.initial {
          top: 32px;
          right: 36px;
        }
      }

      & + button svg {
        width: 24px;
        height: 24px;
        margin-right: 7px;
      }
    }
  }

  .scrollarea {
    height: 100%;
    overflow-y: auto;
    margin: 0 10px;
    width: 100%;
    -webkit-overflow-scrolling: touch;

    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 10px;
    }

    &::after {
      bottom: 0;
      left: 0;
      background: linear-gradient(rgba(255, 255, 255, 0.9) 100%, rgba(255, 255, 255, 0) 0%);
    }

    &::before {
      left: 0;
      background: linear-gradient(rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0) 100%);
    }
  }

  .scrollarea-mobile {
    height: 100%;
    overflow-y: auto;
    position: relative;
    padding: 10% 5% 20px;
    width: 100%;
    -webkit-overflow-scrolling: touch;

    @include media-portrait {
      padding: 22% 14% 20px;
    }

    @include media-mobile-xl {
      padding: 22% 20px 20px;
    }
  }

  .content {
    min-height: 100%;
    // margin-top: 10%;
    // padding-top: 5px;
    // padding-top: 10%;
    padding-bottom: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &:focus {
      border: none;
      outline: none;
    }

    @include media-portrait {
      padding-bottom: 10%;
    }
  }
}

:global {
  .scrollarea-content {
    min-height: 100%;
    margin-top: 10%;
    padding-top: 5px;
    padding-bottom: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &:focus {
      border: none;
      outline: none;
    }
  }

  .scrollarea {
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 0 15.5%;

    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 78%;
      height: 15px;
    }

    &::after {
      bottom: 0;
      left: 0;
      background: linear-gradient(rgba(255, 255, 255, 0.9) 100%, rgba(255, 255, 255, 0) 0%);
    }

    &::before {
      left: 0;
      background: linear-gradient(rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0) 100%);
    }

    @include media-mobile {
      height: calc(100vh - 6rem);
    }
  }

  .transition-enter {
    opacity: 0;
  }

  .transition-enter-active {
    opacity: 1;
    transition: opacity 400ms ease-in;
  }

  .transition-exit {
    opacity: 1;
  }

  .transition-exit-active {
    opacity: 0;
    transition: opacity 400ms ease-in;
  }
}
