@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  position: fixed;
  left: 0;
  top: $height-header;
  width: 100%;
  z-index: 1001;

  @include media-mobile {
    top: $height-header-mobile;
  }

  &.fixedHeader {
    top: $fixed-height-header;
  }

  &.openPopup {
    top: 0;
    z-index: 1003;
  }
}

.header {
  min-height: 60px;
  padding-top: 18px;
  padding-bottom: 16px;
  padding-left: 7.8%;
  padding-right: 7.1%;
  display: flex;
  background-color: $color-white-lilac;
}

.title {
  text-align: left;
  font-size: 14px;
  margin-right: 20px;
  line-height: 1.79;
  font-weight: 300;
  color: $color-electric-violet;
}

.cross {
  margin-left: auto;
  margin-top: -6px;

  svg {
    fill: $color-gray;
  }
}
