.root {
  position: absolute;
  left: 0;
  z-index: 1;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s, visibility 1s;
  line-height: inherit;
  font-size: inherit;

  &.active {
    z-index: 10;
    opacity: 1;
    visibility: visible;
  }

  picture,
  source,
  img {
    width: 100%;
    height: 100%;
    font-size: 0;
    line-height: 0;
    object-fit: cover;
  }
}
