@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  position: relative;

  ul {
    padding: 0;

    a {
      color: $color-black;
      font-size: 15px;
      letter-spacing: 1px;
      font-weight: 900;
      text-transform: uppercase;
      margin-right: 45px;
      text-decoration: none;
      padding-bottom: 2px;
      cursor: pointer;
      transition: color $default-duration-animation $default-bezier;

      @include hover {
        color: $color-hover;
      }

      &:active {
        color: $color-hover;
      }
    }
  }

  &.isSafari {
    ul {
      a {
        padding: 0.7px;
      }
    }
  }

  .scrollSpy {
    position: absolute;
    opacity: 0;
    height: 3px;
    bottom: -5px;
    background-color: $color-hover;
    transition-delay: 0s;
    transition-duration: $big-duration-animation;
    transition-property: left, width, opacity;
    transition-timing-function: $in-bezier;

    &.in {
      opacity: 1;
    }
  }
}
