@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  position: relative;

  .title {
    text-align: left;
    margin-bottom: 11px;

    @include media-desktop {
      font-size: 24px;
      letter-spacing: 0.3px;
    }

    @include media-portrait {
      margin-bottom: 5px;
    }

    @include before-media-mobile {
      font-size: 18px;
      letter-spacing: 0.2px;
    }
  }
}

@keyframes text_opacity {
  100% {
    opacity: 1;
  }
}

.sliderTitle {
  font-size: 50px;
  font-weight: 900;
  line-height: 1.4;
  letter-spacing: 1px;
  color: $color-dusty-gray;
  transition: opacity 1.3s ease-out;
  margin-bottom: 38px;

  @include media-desktop {
    margin-bottom: 17px;
  }

  @include media-portrait {
    font-size: 45px;
    font-weight: 900;
    line-height: 1.56;
    letter-spacing: 0.9px;
  }

  @include before-media-mobile {
    font-size: 30px;
    line-height: 1.33;
    letter-spacing: 0.6px;
  }
}

.marker {
  width: 60px;
  height: 4px;
  background-color: $color-aqua;

  @include before-media-mobile {
    display: none;
  }
}

.sliderContent {
  display: inline;
  color: $color-text;
  word-wrap: normal;
  white-space: nowrap;

  @media (max-width: 600px) {
    left: 0;
    top: -12px;
  }
}

.sliderWrapper {
  position: relative;
  display: inline-block;
  line-height: inherit;

  @media (max-width: 600px) {
    width: 100%;
  }
}

.stub {
  opacity: 0;
  visibility: hidden;
}
