html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: 'Montserrat';
  src: url("./fonts/Montserrat-Black.woff2") format("woff2"), url("./fonts/Montserrat-Black.woff") format("woff"), url("./fonts/Montserrat-Black.ttf") format("truetype");
  font-style: normal;
  font-weight: 900;
}

@font-face {
  font-family: 'Montserrat';
  src: url("./fonts/Montserrat-Bold.woff2") format("woff2"), url("./fonts/Montserrat-Bold.woff") format("woff"), url("./fonts/Montserrat-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: 'Montserrat';
  src: url("./fonts/Montserrat-Regular.woff2") format("woff2"), url("./fonts/Montserrat-Regular.woff") format("woff"), url("./fonts/Montserrat-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: 'Montserrat';
  src: url("./fonts/Montserrat-Light.woff2") format("woff2"), url("./fonts/Montserrat-Light.woff") format("woff"), url("./fonts/Montserrat-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: 300;
}

body {
  font-family: 'Montserrat', Helvetica, Arial, Verdana, Tahoma, sans-serif;
  font-weight: 300;
  color: #000;
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: normal;
  min-width: 320px;
  padding-top: 97px;
  background-color: #fff;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

button,
input {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  color: inherit;
  background: none;
}

input::-ms-clear {
  display: none;
}

button:focus {
  outline: none;
}

input:focus {
  outline: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid transparent;
  -webkit-text-fill-color: #000;
  -webkit-box-shadow: 0 0 0 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
}

:global .resize * {
  transition: none;
}

:global .container {
  padding-left: 10.9%;
  padding-right: 10.9%;
}

@media (max-width: 1440px) {
  :global .container {
    padding-left: 7.8%;
    padding-right: 7.8%;
  }
}

@media (max-width: 1280px) {
  :global .container {
    padding-left: 6.2%;
    padding-right: 6.2%;
  }
}

@media (max-width: 1024px) {
  :global .container {
    padding-left: 5.5%;
    padding-right: 5.5%;
  }
}

@media (max-width: 769px) {
  :global .container {
    padding-left: 4.4%;
    padding-right: 4.4%;
  }
}

@media (max-width: 320px) {
  :global .container {
    padding-left: 6.2%;
    padding-right: 6.2%;
  }
}

:global .headingH2 {
  font-size: 40px;
  line-height: normal;
  font-weight: 900;
  text-align: center;
  letter-spacing: 1.5px;
  color: #000;
}

@media (max-width: 420px) {
  :global .headingH2 {
    font-size: 30px;
    line-height: 1.33;
    letter-spacing: 0.6px;
  }
}

:global .headingH3 {
  font-size: 30px;
  line-height: normal;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.4px;
  color: #000;
}

:global .simpleText {
  font-size: 16px;
  font-weight: 300;
  line-height: 2.19;
  letter-spacing: 0.4px;
  text-align: center;
  color: #000;
}

@media (max-width: 420px) {
  :global .simpleText {
    font-size: 12px;
    line-height: 2.08;
    letter-spacing: 0.3px;
  }
}

:global .error-enter {
  opacity: 0.01;
}

:global .error-enter.error-enter-active {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

:global .error-leave {
  opacity: 1;
}

:global .error-leave.error-leave-active {
  opacity: 0.01;
  transition: opacity 0.3s ease-in;
}

:global .fade-enter {
  opacity: 0.01;
}

:global .fade-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

:global .fade-exit {
  opacity: 1;
}

:global .fade-exit-active {
  opacity: 0.01;
  transition: opacity 500ms ease-in;
}
