@import '~styles/variables.scss';
@import '~styles/mixins.scss';

.root {
  position: relative;
  overflow: visible;
}

.gradient {
  background-image: linear-gradient(-180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.45) 33%, rgba(255, 255, 255, 0.6) 54%, rgba(255, 255, 255, 0.42) 78%, rgba(255, 255, 255, 0) 100%);
  position: absolute;
  left: 0;
  width: 100%;
  height: 130px;
  top: 23vh;
  z-index: 2;

  @include media-hd {
    top: 23vh;
  }

  @include media-desktop {
    top: 22.5vh;
  }

  @include media-portrait {
    top: 25vh;
  }

  @include before-media-mobile {
    top: 18vh;
  }
}

.stickyWrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: calc(100% + 1px);
  clip: rect(0, auto, auto, 0);
}

.sticky {
  position: sticky;
  display: block;
  width: 52vh;
  max-width: 100%;
  height: 17.25vh;
  top: 73.5vh;
  margin: auto;
  transform-origin: center top;
  z-index: 1;
  backface-visibility: hidden;

  @include media-hd {
    top: 71.5vh;
  }

  @include media-portrait {
    top: 67.5vh;
    width: 42.5vh;
    height: 13.1vh;
  }

  @include before-media-mobile {
    top: 69vh;
    width: 46vh;
    height: 15.25vh;
  }

  @include edge_check {
    position: absolute;
    left: 0;
    right: 0;
  }
}

.first,
.second {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.first {
  z-index: 3;
}

.second {
  z-index: 1;
}

.wrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;

  @include media-portrait {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.content {
  width: 100%;
  margin-top: 23vh;
  padding-left: 2.7%;
  margin-bottom: 60px;

  @include media-hd {
    margin-top: 23vh;
    padding-left: 3.3%;
  }

  @include media-desktop {
    margin-top: 22.5vh;
    padding-left: 3.7%;
    margin-bottom: 40px;
  }

  @include media-landscape {
    padding-left: 4.4%;
  }

  @include media-portrait {
    margin-top: 25vh;
    padding-left: 5.6%;
  }

  @include before-media-mobile {
    margin-top: 18vh;
    padding-left: 4%;
  }
}

.form {
  @include media-portrait {
    margin-top: auto;
    margin-bottom: 4.1vh;
  }

  @include before-media-mobile {
    margin-left: -9px;
    width: calc(100% + 18px);
    margin-bottom: 35px;
  }
}

.bublikWrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  max-width: 100%;
  height: 100%;
  overflow: visible;
  clip: rect(0, auto, auto, 0);
}

.likeText {
  height: 100%;
  width: 100%;
}
