@import '~styles/variables.scss';
@import '~styles/mixins.scss';

$max-portrait-width: 600px;

@mixin max-portrait {
  @media (max-width: $max-portrait-width) {
    @content;
  }
}

.root {
  padding: 20px 0;

  .input,
  .textarea {
    width: 100%;
    margin-bottom: 20px;
    padding: 34px 45px 34px 40px;

    &.errored {
      border-color: $color-electric-violet;
      color: $color-electric-violet;
    }

    @include max-portrait {
      padding: 20px;
    }
  }

  .textarea {
    height: 270px;
    resize: vertical;

    @include max-portrait {
      height: 170px;
    }
  }

  .lastInput {
    margin-bottom: 0;
  }
}

.title {
  margin-bottom: 60px;
}

.form {
  font-style: normal;
}

.button {
  width: 300px;
  margin-top: 32px;
  position: relative;
  z-index: 1;

  @include max-portrait {
    width: 100%;
  }
}
