@import '~styles/variables.scss';
@import '~styles/mixins.scss';

:global {
  .team-resize {
    transition: none;
  }
}

.root {
  flex-grow: 1;
  flex-shrink: 1;
  position: relative;
  overflow: hidden;
  text-align: center;

  img {
    max-width: 100%;
    vertical-align: middle;
  }

  .description {
    padding: 0 5px;
    opacity: 0;
    height: 47%;
    bottom: 0;
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $color-white;
    transition: opacity $default-duration-animation $default-bezier;

    & > :first-child {
      font-size: 23px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.17;
      padding-bottom: 20px;
    }

    & > :last-child {
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 600;
    }

    @include before-media-mobile {
      height: 36%;
      justify-content: flex-start;

      & > :first-child {
        font-size: 13px;
        line-height: 16px;
        padding-bottom: 10px;
      }

      & > :last-child {
        font-size: 10px;
        line-height: 18px;
      }
    }

    @include media-mobile {
      & > :first-child {
        font-size: 23px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.17;
        padding-bottom: 20px;
      }

      & > :last-child {
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 600;
      }
    }
  }

  &:hover,
  &.hover {
    .description {
      opacity: 1;
    }
  }

  flex-basis: 20%;
  max-width: 20%;

  @include media-full-hd {
    flex-basis: 20%;
    max-width: 20%;
  }

  @include media-hd {
    flex-basis: 25%;
    max-width: 25%;
  }

  @include media-desktop {
    flex-basis: 25%;
    max-width: 25%;
  }

  @include media-landscape {
    flex-basis: 33.3333%;
    max-width: 33.3333%;
  }

  @include media-portrait {
    flex-basis: 50%;
    max-width: 50%;
  }

  @include media-mobile {
    flex-basis: 100%;
    max-width: 100%;
  }
}
