.root {
  position: absolute;
  left: 100%;
  z-index: 1;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s, visibility 1s;
  line-height: inherit;
  font-size: inherit;
  padding-left: 10px;
  margin-left: -10px;

  &.active {
    z-index: 10;
    opacity: 1;
    visibility: visible;
  }
}
